body {
	margin: 0;
	padding: 0;
	min-height: 300vh;
	font-family: 'Roboto';
	font-size: 16;
	line-height: 1.2;
}

.rubik {
	position: relative;
	width: 100%;
	background: #f3f3f3 url('../img/rubik-background.jpg');
	background-repeat: no-repeat;
	background-size: cover;
}

.rubik__shadow {
	display: none;
}

.rubik__container {
	display: block;

	canvas {
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}
}

.rubik__headline {
	position: relative;
	user-select: none;

	.container {
		padding: 15px;
		max-width: 500px;
	}

	.title {
		font-size: 40px;
		font-weight: 700;
		line-height: 50px;
		text-transform: uppercase;
		color: #333;
		opacity: 0;
	}

	.abstract {
		display: inline-block;
		font-size: 26px;
		font-weight: 700;
		color: #333;
		border-top: 4px solid #333;
		padding-top: 15px;
		padding-right: 50px;
		margin-top: 10px;
		opacity: 0;
	}
}

@media screen and (min-width:1024px) {

	.rubik {
		display: flex;
		justify-content: flex-end;
		min-height: 55vh;
	}

	.rubik__container {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.rubik__headline {
		flex: 1 1 50%;
		max-width: 50%;
		display: flex;
		flex-direction: column;
		justify-content: center;
	}

}

@media screen and (max-width:1023px) {

	.rubik {}

	.rubik__container {
		height: 100vw / 4 * 3;
	}

	.rubik__headline {
		padding-bottom: 60px;
	}

}
